.register-wrapper {
  min-height: 100vh;
  background: linear-gradient(135deg, #f44336, #ff6f61);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
}

.register-card {
  width: 100%;
  max-width: 950px;
  display: flex;
  border-radius: 20px;
  overflow: hidden;
  background: rgba(255,255,255,0.15);
  backdrop-filter: blur(18px);
  box-shadow: 0 30px 60px rgba(0,0,0,0.3);
}

/* LEFT */
.register-left {
  flex: 1;
  padding: 60px;
  color: #fff;
}

.register-left h1 {
  font-size: 2.6rem;
  font-weight: 800;
}

.register-left p {
  margin-top: 15px;
  line-height: 1.8;
  opacity: 0.95;
}

/* RIGHT */
.register-right {
  flex: 1;
  background: #fff;
  padding: 60px;
}

.register-right h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 30px;
}

/* INPUTS */
.input-group {
  position: relative;
  margin-bottom: 22px;
}

.input-group input {
  width: 100%;
  padding: 14px 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 1rem;
}

.input-group label {
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
  background: #fff;
  padding: 0 6px;
  font-size: 0.85rem;
  color: #777;
  transition: 0.25s;
  pointer-events: none;
}

.input-group input:focus + label,
.input-group input:not(:placeholder-shown) + label {
  top: -7px;
  font-size: 0.75rem;
  color: #f44336;
}

/* VERIFY */
.verify-row {
  display: flex;
  gap: 10px;
}

.code-btn {
  padding: 12px 14px;
  border-radius: 8px;
  border: none;
  background: #f44336;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

.code-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* BUTTON */
.register-btn {
  width: 100%;
  margin-top: 10px;
  padding: 14px;
  border-radius: 10px;
  border: none;
  background: linear-gradient(135deg, #f44336, #ff6f61);
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

.register-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(244,67,54,0.4);
}

/* TEXT */
.terms {
  font-size: 0.85rem;
  margin-top: 15px;
}

.login-link {
  margin-top: 20px;
  text-align: center;
}

/* RESPONSIVE */
@media (max-width: 768px) {
  .register-card {
    flex-direction: column;
  }

  .register-left {
    text-align: center;
  }
}
