/* AVATAR CONTAINER */
.doctor-avatar {
  position: relative;
  height: 260px;
  border-radius: 18px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* SOFT BLEND BACKGROUND */
.avatar-bg {
  position: absolute;
  inset: 0;
  background: radial-gradient(
    circle at top,
    rgba(25, 118, 210, 0.12),
    rgba(0, 0, 0, 0.05)
  );
  z-index: 1;
}

/* IMAGE â€” FULLY VISIBLE */
.doctor-avatar img {
  position: relative;
  z-index: 2;
  max-width: 90%;
  max-height: 90%;
  object-fit: contain;
  filter: drop-shadow(0 12px 25px rgba(0, 0, 0, 0.15));
  transition: transform 0.4s ease;
}

/* SUBTLE HOVER LIFE */
.doctor-glass-card:hover .doctor-avatar img {
  transform: scale(1.04);
}

/* STATUS BADGE */
.status-dot {
  position: absolute;
  bottom: 14px;
  right: 14px;
  z-index: 3;
  padding: 6px 14px;
  font-size: 12px;
  font-weight: 600;
  border-radius: 20px;
  color: #fff;
}



/* STATUS */
.status-dot {
  position: absolute;
  bottom: 14px;
  right: 14px;
  padding: 6px 14px;
  font-size: 12px;
  font-weight: 600;
  border-radius: 20px;
  color: #fff;
}

.status-dot.active {
  background: linear-gradient(135deg, #00c853, #64dd17);
}

.status-dot.inactive {
  background: linear-gradient(135deg, #b71c1c, #e53935);
}

/* BODY */
.doctor-body {
  padding-top: 18px;
}

.doctor-body h4 {
  font-weight: 700;
  margin-bottom: 4px;
}

.speciality {
  font-weight: 600;
  color: #1976d2;
  margin-bottom: 6px;
}

.experience {
  font-size: 14px;
  color: #666;
  margin-bottom: 12px;
}

/* RATING */
.rating-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
}

/* FEE */
.fee {
  font-size: 20px;
  font-weight: 700;
  color: #111;
  margin-bottom: 18px;
}

/* ACTIONS */
.action-row {
  display: flex;
  gap: 12px;
}

.btn-outline {
  flex: 1;
  text-align: center;
  padding: 10px 0;
  border-radius: 30px;
  border: 2px solid #1976d2;
  color: #1976d2;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
}

.btn-outline:hover {
  background: #1976d2;
  color: #fff;
}

.btn-primary {
  flex: 1;
  text-align: center;
  padding: 10px 0;
  border-radius: 30px;
  background: linear-gradient(135deg, #1976d2, #42a5f5);
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  transition: opacity 0.3s ease;
}

.btn-primary:hover {
  opacity: 0.85;
}

/* EMPTY */
.empty-state {
  text-align: center;
  padding: 80px 20px;
}

.empty-state h3 {
  font-weight: 700;
}

.empty-state p {
  color: #666;
}

/* LOADER */
.loader-center {
  display: flex;
  justify-content: center;
  margin: 40px 0;
}

/* RESPONSIVE */
 @media (max-width: 576px) {
  .doctor-avatar {
    height: 200px;
  }
}
 