:root {
  --primary: #5f2eea;
  --secondary: #00c9a7;
  --dark: #1f2937;
  --muted: #6b7280;
  --bg: #f4f6fb;
  --card: #ffffff;
  --radius: 18px;
}

/* PAGE WRAPPER */
.dp-page {
  background: var(--bg);
  min-height: 100vh;
  
}

/* HERO HEADER */
.dp-hero {
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  padding: 60px 20px 90px;
  color: #fff;
}

.dp-hero-inner {
  max-width: 1100px;
  margin: auto;
  display: flex;
  gap: 24px;
  align-items: center;
  flex-wrap: wrap;
}

.dp-avatar {
  width: 140px;
  height: 140px;
  border-radius: 22px;
  object-fit: cover;
  border: 4px solid rgba(255,255,255,.5);
}

.dp-name {
  font-size: 28px;
  font-weight: 900;
}

.dp-specialty {
  opacity: .95;
  font-weight: 600;
}

/* CONTENT */
.dp-content {
  max-width: 1100px;
  margin: -60px auto 40px;
  padding: 0 20px;
  display: grid;
  grid-template-columns: 1fr 360px;
  gap: 24px;
}

/* CARD */
.dp-card {
  background: var(--card);
  border-radius: var(--radius);
  padding: 22px;
  box-shadow: 0 20px 45px rgba(0,0,0,.08);
}

/* METRICS */
.dp-metrics {
  display: flex;
  justify-content: space-between;
  margin-top: 14px;
}

.dp-metric {
  text-align: center;
}

.dp-metric span {
  font-size: 22px;
  font-weight: 800;
  display: block;
}

/* AVAILABILITY */
.dp-slot {
  background: #f9fafb;
  border-left: 4px solid var(--primary);
  padding: 10px 14px;
  border-radius: 10px;
  margin-bottom: 10px;
}

/* BUTTONS */
.dp-btn {
  width: 100%;
  padding: 14px;
  border-radius: 14px;
  font-weight: 800;
  border: none;
  cursor: pointer;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  color: #fff;
}

/* MODAL */
.dp-modal {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.dp-modal-box {
  background: #fff;
  width: 95%;
  max-width: 420px;
  padding: 24px;
  border-radius: 20px;
}

/* RESPONSIVE */
@media (max-width: 900px) {
  .dp-content {
    grid-template-columns: 1fr;
  }
}
