/* ROOT */
.login-wrapper {
  min-height: 100vh;
  background: linear-gradient(135deg, #f44336, #ff7961);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
}

/* CARD */
.login-card {
  width: 100%;
  max-width: 900px;
  display: flex;
  border-radius: 18px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(18px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25);
}

/* LEFT PANEL */
.login-left {
  flex: 1;
  padding: 50px;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.login-left h1 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 15px;
}

.login-left p {
  font-size: 1rem;
  line-height: 1.7;
  opacity: 0.95;
}

/* RIGHT PANEL */
.login-right {
  flex: 1;
  background: #ffffff;
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.login-right h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 30px;
  color: #333;
}

/* INPUT GROUP */
.input-group {
  position: relative;
  margin-bottom: 25px;
}

.input-group input {
  width: 100%;
  padding: 14px 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 1rem;
  outline: none;
}

.input-group label {
  position: absolute;
  top: 50%;
  left: 12px;
  color: #888;
  font-size: 0.9rem;
  transform: translateY(-50%);
  pointer-events: none;
  transition: 0.3s ease;
  background: #fff;
  padding: 0 5px;
}

.input-group input:focus + label,
.input-group input:not(:placeholder-shown) + label {
  top: -7px;
  font-size: 0.75rem;
  color: #f44336;
}

/* BUTTON */
.login-btn {
  width: 100%;
  padding: 14px;
  border-radius: 10px;
  border: none;
  background: linear-gradient(135deg, #f44336, #ff7961);
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s ease;
}

.login-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(244, 67, 54, 0.4);
}

.login-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* LINKS */
.login-links {
  margin-top: 20px;
  text-align: center;
  font-size: 0.9rem;
}

.login-links a {
  color: #f44336;
  text-decoration: none;
  font-weight: 500;
}

.login-links span {
  margin: 0 10px;
  color: #aaa;
}

/* RESPONSIVE */
@media (max-width: 768px) {
  .login-card {
    flex-direction: column;
  }

  .login-left {
    text-align: center;
  }
}
