.change-wrapper {
  min-height: 100vh;
  background: linear-gradient(135deg, #f44336, #ff6f61);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
}

.change-card {
  width: 100%;
  max-width: 900px;
  display: flex;
  border-radius: 20px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(18px);
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.3);
}

/* LEFT */
.change-left {
  flex: 1;
  padding: 60px;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.change-left h1 {
  font-size: 2.4rem;
  font-weight: 800;
  margin-bottom: 15px;
}

.change-left p {
  font-size: 1rem;
  line-height: 1.7;
  opacity: 0.95;
}

/* RIGHT */
.change-right {
  flex: 1;
  background: #fff;
  padding: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.change-right h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 30px;
}

/* INPUT */
.input-group {
  position: relative;
  margin-bottom: 22px;
}

.input-group input {
  width: 100%;
  padding: 14px 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 1rem;
}

.input-group label {
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
  background: #fff;
  padding: 0 6px;
  font-size: 0.85rem;
  color: #777;
  pointer-events: none;
  transition: 0.25s;
}

.input-group input:focus + label,
.input-group input:not(:placeholder-shown) + label {
  top: -7px;
  font-size: 0.75rem;
  color: #f44336;
}

/* BUTTON */
.submit-btn {
  width: 100%;
  padding: 14px;
  border-radius: 10px;
  border: none;
  background: linear-gradient(135deg, #f44336, #ff6f61);
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s ease;
}

.submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(244, 67, 54, 0.4);
}

.submit-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* LINKS */
.change-links {
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
  font-size: 0.9rem;
}

.change-links a {
  color: #f44336;
  font-weight: 600;
  text-decoration: none;
}

/* RESPONSIVE */
@media (max-width: 768px) {
  .change-card {
    flex-direction: column;
  }

  .change-left {
    text-align: center;
    padding: 40px;
  }

  .change-right {
    padding: 40px;
  }
}
