.modern-footer {
    background: linear-gradient(135deg, #0f172a, #020617);
    color: #e5e7eb;
    padding: 50px 20px 0;
    font-family: "Inter", sans-serif;
}

.footer-wrapper {
    max-width: 1200px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
}

/* Brand section */
.footer-brand h3 {
    font-size: 24px;
    letter-spacing: 1px;
    margin-bottom: 8px;
    color: #ffffff;
}

.footer-brand p {
    font-size: 14px;
    color: #9ca3af;
    line-height: 1.6;
}

/* Links */
.footer-links {
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
}

.footer-links a {
    font-size: 14px;
    color: #cbd5f5;
    text-decoration: none;
    position: relative;
    transition: color 0.3s ease;
}

.footer-links a::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -4px;
    width: 0;
    height: 2px;
    background: #38bdf8;
    transition: width 0.3s ease;
}

.footer-links a:hover {
    color: #38bdf8;
}

.footer-links a:hover::after {
    width: 100%;
}

/* Bottom strip */
.footer-bottom {
    margin-top: 40px;
    border-top: 1px solid rgba(255, 255, 255, 0.08);
    padding: 15px 0;
    text-align: center;
    font-size: 13px;
    color: #9ca3af;
}

.footer-bottom a {
    color: #38bdf8;
    text-decoration: none;
    margin-left: 5px;
}

.footer-bottom a:hover {
    text-decoration: underline;
}

/* Responsive */
@media (max-width: 768px) {
    .footer-wrapper {
        flex-direction: column;
        text-align: center;
    }

    .footer-links {
        justify-content: center;
    }
}
