.hero-wrapper {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #0f2a44, #14448a);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.hero-swiper {
  width: 100%;
  max-width: 1300px;
}

.hero-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-glass {
  width: 100%;
  height: 80vh;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(18px);
  border-radius: 24px;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.25);
  animation: float 6s ease-in-out infinite;
}

.hero-image {
  max-width: 100%;
  max-height: 60vh;
  object-fit: contain;
  transition: transform 0.5s ease;
}

.hero-image:hover {
  transform: scale(1.03);
}

.hero-caption {
  margin-top: 1.5rem;
  text-align: center;
  color: #ffffff;
}

.hero-caption h1 {
  font-size: 2.2rem;
  font-weight: 600;
  letter-spacing: 0.5px;
}

/* Pagination */
.swiper-pagination-bullet {
  background: rgba(255, 255, 255, 0.6);
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background: #ffffff;
}

/* Navigation */
.swiper-button-next,
.swiper-button-prev {
  color: #ffffff;
}



/* Custom Navigation Wrapper */
.hero-nav {
  position: absolute;
  bottom: 30px;
  right: 30px;
  display: flex;
  gap: 14px;
  z-index: 20;
}

/* Base Button Style */
.hero-prev,
.hero-next {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  font-size: 28px;
  font-weight: 300;
  color: #ffffff;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.25),
    rgba(255, 255, 255, 0.05)
  );
  backdrop-filter: blur(14px);
  box-shadow:
    0 8px 20px rgba(0, 0, 0, 0.25),
    inset 0 0 0 1px rgba(255, 255, 255, 0.3);
  transition: all 0.35s ease;
}

/* Hover Effect */
.hero-prev:hover,
.hero-next:hover {
  transform: scale(1.1);
  box-shadow:
    0 12px 30px rgba(0, 0, 0, 0.4),
    0 0 25px rgba(255, 255, 255, 0.4);
}

/* Click Feedback */
.hero-prev:active,
.hero-next:active {
  transform: scale(0.95);
}

/* Arrow Alignment */
.hero-prev {
  padding-right: 2px;

}

.hero-next {
  padding-left: 2px;
}

/* Mobile Optimisation */
@media (max-width: 768px) {
  .hero-nav {
    bottom: 18px;
    right: 18px;
  }

  .hero-prev,
  .hero-next {
    width: 44px;
    height: 44px;
    font-size: 22px;
  }
}


/* Floating animation */
@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
  100% {
    transform: translateY(0);
  }
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .hero-wrapper {
    min-height: 70vh;
    padding: 1rem;
  }

  .hero-glass {
    height: 55vh;
    padding: 1.2rem;
  }

  .hero-caption h1 {
    font-size: 1.5rem;
  }

  .hero-image {
    max-height: 40vh;
  }
}
