.pagination {
    display: flex;
    list-style: none;
    padding: 0;
    gap: 8px;
}

.page-item button {
    padding: 8px 14px;
    border-radius: 6px;
    border: 1px solid #ddd;
    background: white;
    cursor: pointer;
    transition: 0.3s;
    font-size: 14px;
}

.page-item.active button {
    background: #007bff;
    color: white;
    border-color: #007bff;
}

.page-item.disabled button {
    opacity: 0.5;
    cursor: not-allowed;
}

.page-item button:hover {
    background: #f0f0f0;
}

@media(max-width: 500px) {
    .pagination {
        flex-wrap: wrap;
        justify-content: center;
    }
    .page-item button {
        padding: 6px 10px;
        font-size: 13px;
    }
}
