/* PAGE */
.search-page {

  padding: 20px 5px;
 

}

/* HEADER */
.search-title {
  text-align: center;
  margin-bottom: 32px;
}

.search-title h1 {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 6px;
}

.search-title p {
  color: #64748b;
  font-size: 1rem;
}

/* FLOATING BAR */
.search-bar {
  /* max-width: 900px; */
  margin: auto;
  background: #fff;
  border-radius: 999px;
    padding: 10px 14px;
/*  padding-left: 5px ;
 padding-right: 5px; */
  display: flex;
  align-items: center;
  gap: 12px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.08);
}

/* INPUT */
.search-input {
  flex: 1;
     border: none;
  outline: none;
 
  font-size: 1rem;
  padding: 10px;
}

/* SELECT */
.search-select {
  border: none;
  outline: none;
  background: transparent;
  margin-bottom: 0px;
  width: 40%;
  font-size: 0.95rem;
  color: #475569;
  cursor: pointer;
}

/* ICON BUTTONS */
.icon-btn {
  background: none;
  border: none;
  cursor: pointer;
  color: #2563eb;
  font-size: 0.95rem;
  font-weight: 500;
}

/* GO BUTTON */
.go-btn {
  background: #2563eb;
  color: #fff;
  border: none;
  border-radius: 999px;
  padding: 10px 18px;
  cursor: pointer;
  font-weight: 500;
}

.go-btn:hover {
  background: #1d4ed8;
}

/* RECOMMENDATIONS */
.recommend {
  max-width: 900px;
  margin: 8px auto 0;
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.12);
  overflow: hidden;
}

.recommend div {
  padding: 12px 16px;
  cursor: pointer;
}

.recommend div:hover {
  background: #f1f5ff;
}

/* FILTERS */
.filters {
  max-width: 900px;
  margin: 16px auto 0;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 12px;
}

.filters input,
.filters select {
  padding: 10px 12px;
  border-radius: 12px;
  border: 1px solid #c7d2fe;
  font-size: 0.9rem;
}

/* DOCTORS */
.doctor-list {
  max-width: 900px;
  margin: 48px auto 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.doctor-card {
  background: #fff;
  border-radius: 18px;
  padding: 20px;
  border: 1px solid #eef2ff;
}

.doctor-card h4 {
  margin: 0 0 6px;
  font-weight: 600;
}

.doctor-card p {
  margin: 0;
  color: #64748b;
  font-size: 0.9rem;
}

/* MOBILE */
@media (max-width: 900px) {
  .filters {
    grid-template-columns: 1fr;
  }

  .doctor-list {
    grid-template-columns: 1fr;
  }

  .search-bar {
    flex-wrap: wrap;
    border-radius: 20px;
  }
}
